/*
Jumbotron

Jumbotron are hero content areas. Larger marketing text with a colored backgrounds to grab the user or mark the page as important. By default Jumbotron is dark text on a light grey background the UC's add some predefined colored backgrounds allow for modificaiton of the area. The backgrounds were generated by this online generator https://qrohlf.com/trianglify-generator/.
| Class        | Effect                                               |
| ------------ | ---------------------------------------------------- |
| .orange-blue | orange to blue background                            |
| .orange-grey | orange to grey background                            |
| .blue-grey   | blue to grey background                              |
| .soft        | softens the edges of the triangles in the background |
| .blur        | blurs the back to a smooth gradient                  |

.orange-blue 		      - 	orange to blue background
.orange-grey 		      - 	orange to grey background
.blue-grey 			      - 	blue to grey background
.soft				      - 	softens the edges of the triangles in the background 
.blur.blue-grey 	      - 	blurs the back to a smooth gradient

 Markup:
<div class="jumbotron relative overflow_hidden br_round {{modifier_class}}">
   <div class="container p-x_4 max-w_4 w_auto m_auto">
         <h1 class="c_black font_10:lg font_6 font_8:md">I am a Jumbotron!</h1>
         <p class="font_1 font_1:md font_3:lg">Doloribus omnis facere voluptatibus beatae.</p>
         <div><a class="br_radius btn btn-primary gradient_teal h:gradient_teal-reverse" href="#" role="button">Learn more</a></div>
   </div>
</div>

Weight: 50

Styleguide Recipes CVQualtiy.Containers.Jumbotron

*/
.jumbotron:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1;
  display: block;
  background: url("../img/cvquality/pattern-grey.png") no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
.jumbotron.orange-blue:before {
  background-image: url("../img/cvquality/pattern-orange-blue.png");
}
.jumbotron.orange-grey:before {
  background-image: url("../img/cvquality/pattern-orange-grey.png");
}
.jumbotron.blue-grey:before {
  background-image: url("../img/cvquality/pattern-blue-grey.png");
}
.jumbotron.soft:before {
  -webkit-filter: blur(3px);
  -moz-filter: blur(3px);
  -o-filter: blur(3px);
  -ms-filter: blur(3px);
  filter: blur(3px);
  top: -3px;
  bottom: -3px;
  left: -3px;
  right: -3px;
}
.jumbotron.blur:before {
  -webkit-filter: blur(25px);
  -moz-filter: blur(25px);
  -o-filter: blur(25px);
  -ms-filter: blur(25px);
  filter: blur(25px);
  top: -25px;
  bottom: -25px;
  left: -25px;
  right: -25px;
}
.jumbotron .container {
  z-index: 3;
}

