*[class*=triangle] {
  position: relative;
}

*[class*=triangle]::before {
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  position: absolute;
}

.triangle_left::before,
.triangle_right::before {
  top: 50%;
  margin-top: -0.5em;
}

.triangle_top::before,
.triangle_bottom::before {
  left: 50%;
  margin-left: -0.5em;
}

.triangle_left {
  margin-left: 0.75em;
}

.triangle_right {
  margin-right: 0.75em;
}

.triangle_top {
  margin-top: 0.75em;
}

.triangle_bottom {
  margin-bottom: 0.75em;
}

.triangle_left::before {
  left: -0.75em;
  border-width: 0.5em 0 0.5em 0.75em;
  border-left-color: inherit;
  border-right-color: #0000;
  border-bottom-color: #0000;
  border-top-color: #0000;
}

.triangle_right::before {
  right: -0.75em;
  border-width: 0.5em 0.75em 0.5em 0;
  border-left-color: #0000;
  border-right-color: inherit;
  border-bottom-color: #0000;
  border-top-color: #0000;
}

.triangle_top::before {
  top: -0.75em;
  border-width: 0.75em 0.5em 0 0.5em;
  border-left-color: #0000;
  border-right-color: #0000;
  border-bottom-color: #0000;
  border-top-color: inherit;
}

.triangle_bottom::before {
  bottom: -0.75em;
  border-width: 0 0.5em 0.75em 0.5em;
  border-left-color: #0000;
  border-right-color: #0000;
  border-bottom-color: inherit;
  border-top-color: #0000;
}

